import React, { useContext } from "react";
import { ArticleDefaultIcon } from "./ArticleDefaultIcon";
import { PublicationType } from "../utils/types";
import { getTimeWindowFromNow } from "../utils/utils";
import { SentimentBar } from "./SentimentBar";
import { ThemeContext } from "grommet";
import * as S from "../styles";

export const Publication = (props: PublicationType) => {
  const theme: any = useContext(ThemeContext);

  return (
    <S.StyledPublication>
      <SentimentBar sentiment={props.sentiment.sentiment} />

      {props.base.favicon ? (
        <S.ArticleIcon alt="news-icon" src={props.base.favicon} />
      ) : (
        <ArticleDefaultIcon
          color={theme.tickerMarquee.articleText}
          style={{ display: "flex", alignSelf: "center" }}
          width="20px"
          height="20px"
        />
      )}

      <S.StyledTitle target="_" href={props.base.source}>
        {props.base.title}
      </S.StyledTitle>

      <S.Delimiter />

      <S.PublishedTime>
        {getTimeWindowFromNow(props.base.pub_date)}
      </S.PublishedTime>
    </S.StyledPublication>
  );
};
