import React, { Fragment, useEffect, useMemo, useRef, useState } from "react";
import * as S from "./styles";

import { GetPublicationsDocument } from "../../generated/apollo";
import { useQuery } from "@apollo/react-hooks";
import { PublicationType } from "./utils/types";
import { CryptoMoodIcon } from "./components/CryptoMoodIcon";
import { Publication } from "./components/Publication";
import { useAnimationState } from "./hooks/useAnimationState";
import { useLogoAnimation } from "./hooks/useLogoAnimation";

const TickerWidgetComponent = () => {
  const params = useRef(new URLSearchParams(window.location.search));
  const asset = params.current.get("asset") || "BTC";
  const position = params.current.get("position") || "top";

  const positionFlex =
    position === "top"
      ? "flex-start"
      : position === "bottom"
      ? "flex-end"
      : "flex-start";
  const publicationsRef = useRef(null);
  const iconRef = useRef(null);
  const [publications, setPublications] = useState<PublicationType[]>([]);
  const [isLogoOpen, setIsLogoOpen] = useState(false);

  const { data, loading } = useQuery(GetPublicationsDocument, {
    variables: {
      asset: [asset]
    }
  });

  useAnimationState(publicationsRef);
  useLogoAnimation(iconRef, setIsLogoOpen);

  useEffect(() => {
    if (data == null || data.getPublications == null) {
      return;
    }
    setPublications(data.getPublications.entries);
  }, [data]);

  const memoizedPublication = useMemo(() => {
    const publicationItems = publications.map(publication => (
      <Publication key={publication.base.id} {...publication} />
    ));

    return (
      <>
        <S.PublicationsAnimation
          publicationsLength={publicationItems.length}
          className={"animation"}
        >
          {publicationItems}
        </S.PublicationsAnimation>

        <S.PublicationsAnimation
          publicationsLength={publicationItems.length}
          className={"animation"}
        >
          {publicationItems}
        </S.PublicationsAnimation>
      </>
    );
  }, [publications]);

  const assetPublications =
    (data &&
      data.getPublications &&
      Array.isArray(data.getPublications.entries) &&
      data.getPublications.entries.length > 0 &&
      data.getPublications.entries[0]) ||
    null;

  return (
    <S.Container position={positionFlex}>
      <S.StyledTickerMarquee isLogoOpen={isLogoOpen}>
        <S.Publications ref={publicationsRef}>
          {loading && assetPublications ? (
            <p>Loading...</p>
          ) : (
            <Fragment>{memoizedPublication}</Fragment>
          )}
        </S.Publications>
      </S.StyledTickerMarquee>

      <S.StyledIcon
        style={positionFlex === "flex-end" ? { bottom: "0" } : { top: "0" }}
        ref={iconRef}
        target="_"
        href={"https://cryptomood.com"}
      >
        <CryptoMoodIcon width={155} height={40} />
      </S.StyledIcon>
    </S.Container>
  );
};

export default TickerWidgetComponent;
