import { useCallback, useEffect } from "react";

export const useLogoAnimation = (iconRef: any, setIsLogoOpen: any) => {
  const openLogoCallback = useCallback(() => {
    setIsLogoOpen(true);
  }, [setIsLogoOpen]);

  const closeLogoCallback = useCallback(() => {
    setIsLogoOpen(false);
  }, [setIsLogoOpen]);

  useEffect(() => {
    if (iconRef != null && iconRef.current !== null) {
      (iconRef.current as any).addEventListener("mouseenter", openLogoCallback);
      (iconRef.current as any).addEventListener(
        "mouseleave",
        closeLogoCallback
      );
    }
    return () => {
      window.removeEventListener("mouseenter", openLogoCallback);
      window.removeEventListener("mouseleave", closeLogoCallback);
    };
  });
};
