export enum SentimentEnum {
  POSITIVE = "positive",
  NEGATIVE = "negative",
  NEUTRAL = "neutral"
}

/*
 * Method takes as parameter sentiment which has interval -10, 10
 * and returns calculated styles for bar depending on the sentiment value.
 *
 * Sentiment bar is divided into two halves => [ negative | positive ].
 * Positive part of bar starts from middle till end of right part of bar. => [... | {positive part} ]
 * Negative part of bar starts from middle till start of left part of bar. => [ {negative part} | ... ]
 *
 * e.g. sentiment = 65 means it's positive so style left: 50% is applied and then
 * value of sentiment is transformed to percentages which is 15% so style width: 15% is applied.
 *
 * Returns null in case of neutral sentiment.
 */
export const getSentimentBarStyles = (
  sentiment: number
): { width: number; left: number } => {
  let width = 0;
  let left = 0;

  if (sentiment === 0) {
    left = 48.5;
    width = 3;
    return { left, width };
  }

  // calculate sentiment in percentage and split that as positive or negative part of the bar
  const sentimentPercentage = (sentiment * 10) / 2;

  // half of part positive or negative which can bar reach
  const barPercentage = 50;

  if (sentiment > 0) {
    left = barPercentage;
    width = Math.min(sentimentPercentage, barPercentage);
  } else if (sentiment < 0) {
    left =
      sentiment >= -10
        ? Math.min(barPercentage - Math.abs(sentimentPercentage), barPercentage)
        : 0;
    width =
      sentiment >= -10
        ? Math.min(Math.abs(sentimentPercentage), barPercentage)
        : barPercentage;
  }

  return { left, width };
};

export const getTimeWindowFromNow = (timestamp: number): string => {
  let dateNow = new Date().getTime();

  // get timestamp difference
  const differenceInSec = Math.floor(dateNow / 1000) - timestamp;
  const days = Math.floor(differenceInSec / 86400);
  const hours = Math.floor(differenceInSec / 3600);
  const minutes = Math.floor(differenceInSec / 60);
  return days > 0 ? `${days} d` : hours > 0 ? `${hours} h` : `${minutes} min`;
};
