import * as React from "react";
import { getSentimentBarStyles, SentimentEnum } from "../utils/utils";
import * as S from "../styles";

export const SentimentBar = ({ sentiment }: ISentimentBarProps) => {
  const { left, width } = getSentimentBarStyles(sentiment);
  const color =
    sentiment > 0
      ? SentimentEnum.POSITIVE
      : sentiment < 0
      ? SentimentEnum.NEGATIVE
      : SentimentEnum.NEUTRAL;

  return (
    <S.StyledSentimentBar>
      <S.SentimentBarContainer>
        <S.Bar color={color} left={left} width={width} />
      </S.SentimentBarContainer>
    </S.StyledSentimentBar>
  );
};

interface ISentimentBarProps {
  sentiment: number;
}
