import { useCallback, useEffect } from "react";

export const useAnimationState = (publicationsRef: any) => {
  const mouseEnterCallback = useCallback(() => {
    (publicationsRef.current as any).childNodes.forEach((child: any) => {
      child.classList.add("paused");
    });
  }, [publicationsRef]);

  const mouseLeaveCallback = useCallback(() => {
    (publicationsRef.current as any).childNodes.forEach((child: any) => {
      child.classList.remove("paused");
    });
  }, [publicationsRef]);

  useEffect(() => {
    if (publicationsRef != null && publicationsRef.current != null) {
      (publicationsRef.current as any).addEventListener(
        "mouseenter",
        mouseEnterCallback
      );
      (publicationsRef.current as any).addEventListener(
        "mouseleave",
        mouseLeaveCallback
      );
    }
    return () => {
      window.removeEventListener("mouseenter", mouseEnterCallback);
      window.removeEventListener("mouseleave", mouseLeaveCallback);
    };
  });
};
